import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';

import { AlbumService } from '../../angular-services/album.service';
import { Album } from '../../classes/album';

@Component({
  selector: 'app-edit-album-form-admin',
  templateUrl: './edit-album-form.component.html',
  styleUrls: ['./edit-album-form.component.css']
})
export class EditAlbumFormComponent implements OnInit {

  @Output() albumTitle = new EventEmitter<string>();
  alert: any = { type: '', message: '', show: false };
  formEditAlbum: FormGroup;
  album: Album = new Album();

  constructor(private albumService: AlbumService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.initForm();
    this.getAlbum();
  }

  initForm() {
    this.formEditAlbum = new FormGroup({
      title: new FormControl('', Validators.required),
      description: new FormControl('', Validators.required),
      status: new FormControl('', Validators.required)
    });
  }

  getAlbum() {
    let key = this.route.snapshot.paramMap.get('id');
    this.albumService.getAlbum(key).subscribe(
      response => {
        if (response !== undefined) {
          this.album = response;
          this.albumTitle.emit(this.album.title);
          this.loadForm();
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  loadForm() {
    this.formEditAlbum.patchValue({
      title: this.album.title,
      description: this.album.description,
      status: this.album.status
    });
  }

  save() {
    if (this.formEditAlbum.valid) {
      let key = this.route.snapshot.paramMap.get('id');
      let data = this.formEditAlbum.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.albumService.updateAlbum(key, data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            alert('Houve algum erro ao salvar os dados!');
          }
        }
      )
    }
  }

}
